  <div>
    image
    <img
      src="http://localhost:13372/assets/leaflet-marker.png"
      alt=""
      srcset=""
    />

    image
    <LMap
      style="height: 300px"
      :zoom="zoom"
      :center="center ?? [45.815399, 15.966568]"
